<template>
    <div class='form__content'>
        <div class='title'>
            POWER
            <fa-icon class='icon' :icon="['fal','info-circle']" v-on:click='setSelectedWiki()'/>   
        </div>
        <div class='section'>
            <div class='line__item--flex'>                
                <input-select :callback='setPowerSource' :fieldLabel="powerSourceLabel" :options="powerSourceOptions" />                
                <input-number :callback='setSolarArrays' :preset='presetSolarArrays' :fieldLabel="solarArrayLabel"/>
                <input-number :callback='setPowerStorage' :preset='presetPowerStorage' :fieldLabel="powerStorageLabel"/>
                {{presetSolarArrays}}
            </div>
        </div>
    </div>
</template>

<script>
import InputSelect from '@/components/Input_Select';
import InputNumber from '@/components/Input_Number';
import {mapState,mapMutations,mapGetters} from 'vuex'
export default {   
    components:{
        'input-select':InputSelect,
        'input-number':InputNumber,
    },

    data(){
        return{
            powerSourceLabel:'POWER SOURCE',
            powerStorageLabel:'BATTERIES',
            solarArrayLabel:'UNITS',

            powerStorage:0,
            powerSource:'',
            solarArrays:0,

            powerSourceOptions:[
                {
                    'value':'solar',
                    'key':'solar',
                    'text':'Solar Arrays',
                },
                {
                    'value':'isotope',
                    'key':'isotope',
                    'text':'Radio Isotope Generator',
                },

            ],
        }
    },

    methods:{
        ...mapMutations([
            'SETPOWERSTORAGE',
            'SETPOWERSOURCE',
            'SETSOLARRAYS',
            'SETSELECTEDWIKI',
        ]),
        
        setSelectedWiki:function(){
            this.SETSELECTEDWIKI('power');
        },

        setPowerStorage:function(value){
            this.SETPOWERSTORAGE(value);
        },

        setPowerSource:function(value){
            this.SETPOWERSOURCE(value);
        },

        setSolarArrays:function(value){
            this.SETSOLARRAYS(value)
        }
    },

    computed:{
        ...mapGetters({
            presetSolarArrays:'getSolarArrays',
            presetPowerStorage:'getPowerStorage',
        })
    }
}
</script>

<style lang="scss" scoped>
    @import '../scss/_config_form';
        
    .form__content{
        @extend %form__content;

        .title{
            @extend %form__section__title;
            
        }

        .section{
            @extend %form__section;
        }

        .line__item--flex{
            @extend %form__line__item--flex;
        }
    }
</style>
