<template>
    <div class='mission__stats'>
        <div>Location:</div>
        <div>Mars</div>
        <div>Inhabitants:</div>
        <div>{{configData.game_config.human_agent.amount}} Humans</div>
        <div>Duration:</div>
        <div>{{configData.game_config.duration.value}} Sols</div>
        <div>Food:</div>
        <div>{{configData.game_config.food_storage.amount}} kg</div>
        <div>Greenhouse:</div>
        <div>{{(greenhouse).replace(/_/g,' ').toUpperCase()}}</div>
        <div>Crew Quarters:</div>
        <div>{{(crewQuarters).replace(/_/g,' ').toUpperCase()}}</div>
        <div>ECLSS:</div>
        <div>{{eclss}}</div>
        <div>Solar Arrays:</div>
        <div>{{configData.game_config.solar_arrays.amount}} Arrays</div>
        <div>Power Storage:</div>
        <div>{{configData.game_config.power_storage.amount}} Batteries</div>
        <div>Plants:</div>
        <div>{{plantSpecies}} Species</div>

    </div>    
</template>

<script>
import {mapState,mapMutations,mapGetters} from 'vuex'
export default {
    components:{

    },

    computed:{
        ...mapGetters({
            stepData:'getMission',
            configData:'getFormattedConfig',
            crewQuarters:'getCrewQuarters',
            greenhouse:'getGreenhouse',
            eclss:'getECLSS'
        }),

        plantSpecies:function(){ //Fix
            let total = 0;
            this.configData.game_config.plants.forEach(element =>{
                if(element != '' && element != null && element != 'none'){
                    total++;
                }
            })
        },

    }
}
</script>

<style lang="scss" scoped>
    .mission__stats{
        color: white;
        display:grid;
        height: 100%;
        width: 100%;
        grid-template-rows: repeat(auto-fit, 1.5rem);
        grid-template-columns: repeat(2, 1fr);
        grid-row-gap: 4px;

        *{
            color: white;
            font-size: 1rem;
            font-family: 'roboto'
        }
    }
</style>
