import Vue from 'vue';
import Router from 'vue-router';

//ENTRY
import Entry from './views/Entry';
import EntryLogin from './components/Entry_Login';
import EntryBeta from './components/Entry_Beta';
import EntryRegister from './components/Entry_Register';
import EntryMenu from './components/Entry_Menu';


//DASHBOARD
import Dashboard from './views/Dashboard';
//DASHBOARD TOOLBARS
import ToolbarConfig from './components/Toolbar_Config';
import ToolbarSession from './components/Toolbar_Session';
//DASHBOARD CONTENT
import ContentConfig from './components/Content_Config';
import ContentSessionMain from './components/Content_SessionMain'
import ContentAgentFeed from './components/Content_AgentFeed'

//CONFIGURATION
import ConfigurationForm from './components/Configuration_Form';

import FormInitial from './components/Form_Initial';
import FormInhabitants from './components/Form_Inhabitants';
import FormGreenhouse from './components/Form_Greenhouse';
import FormPower from'./components/Form_Power';

//MAIN MENUS
import MenuConfig from './components/Menu_Configuration';
import MenuSession from './components/Menu_Session';

Vue.use(Router);

export default new Router({
  routes: [
  {
    path: '/',
    alias: '/entry',
    name: 'entry',
    component: Entry,

    children:[
      {        
        path:'',
        name: 'welcome',
        component: EntryBeta
      },
      {        
        path:'login',
        name: 'login',
        component: EntryLogin
      },
      { 
        path:'register',
        name: 'register',
        component: EntryRegister
      },
      { 
        path:'menu',
        name: 'menu',
        component: EntryMenu
      },
    ]
  },
  {
    path:'/dashboard',
    alias:'dashboard',
    name: 'dashboard',
    component: Dashboard,

    children:[
      {
        path:'configuration',
        name:'configuration',
        components:{
          toolbar: ToolbarConfig,
          content: ContentConfig,
          menu: MenuConfig,
        },

        children:[
          {
            path:'',
            name:'configuration',
            components:{
              configurationForm:ConfigurationForm,
            },

            children:[
              {
                path:'',
                name:'initial',
                components:{
                  sectionOne: FormInitial,
                  sectionTwo: FormInhabitants,
                  sectionThree: FormGreenhouse,
                  sectionFour: FormPower,
                }
              }
            ]
          }
        ] 
      },
      {
        path:'session',
        name:'session',
        components:{
          toolbar:ToolbarSession,
          content:ContentSessionMain,
          menu: MenuSession,
        },
      },
      {
        path:'livefeed',
        name:'livefeed',
        components:{
          toolbar:ToolbarSession,
          content:ContentAgentFeed,
        },
        props:{dashboardMain:false,liveAgentFeed:true}
      },
    ]
  }
]});
