<template>
    <div class='atmospheric-composition-content' style='color:white'>
        <chart-pie :chartID ="'1'" :chartColors="['#fdb53f','#3399db','#1bbc9b','#ff7777','#f15928','#c1333f','#674ab4']" :chartBuild="atmosphericChartData"/>
    </div>    
</template>

<script>
import {mapState,mapMutations,mapGetters} from 'vuex'
import ChartPie from '@/components/Chart-Pie'
export default {

    components:{
        'chart-pie':ChartPie,
    },



    data(){
        return{

        }
    },

    computed:{
        ...mapGetters({
            panelData:'getEcosystem',
        }),

        atmosphericChartData:function(){
            let chartData ={
                'labels':['N2','CO2','O2','H2O','H2','CH4'],
                'data':[],
            }
            chartData.data.push(this.panelData.n2Ratio);
            chartData.data.push(this.panelData.co2Ratio);
            chartData.data.push(this.panelData.o2Ratio);
            chartData.data.push(this.panelData.h2oRatio);
            chartData.data.push(this.panelData.h2Ratio);            
            chartData.data.push(this.panelData.ch4Ratio);
        
            return chartData;
        }
    }
}
</script>

