<template>
    <div class='main__menu'>
        <div class='menu__container'>
            <div class='title'>
                <div class='title__container'>
                    <div class='title__text'>MAIN MENU</div>
                    <div class='menu__close' v-on:click='closeMenu()'>
                        <fa-icon class='' :icon="['fal','times']"/>
                    </div>
                </div>
            </div>
            <button class='form__multi__btn' v-on:click='test()' disabled>UPLOAD<br/>CONFIGURATION</button>                
            <button class='form__multi__btn' v-on:click='downloadConfiguration()'>DOWNLOAD<br/>CONFIGURATION</button>
            <button class='form__multi__btn' v-on:click='test()' disabled>SAVE<br/>CONFIGURATION</button>
            <button class='form__multi__btn' v-on:click= 'test()' disabled>RESET<br/>DEFAULTS</button>
            <button class='form__submit__btn' v-on:click= 'test()' disabled>OPTIONS</button>
            <button class='form__submit__btn form__submit__btn--logout' v-on:click='logoutUser()'>LOGOUT</button>
            <button class='form__submit__btn' v-on:click='closeMenu()'>EXIT MENU</button> 
        </div>
    </div>
</template>

<script>
import ConnectionHandler from '@/javascript/connectionHandler'
import DownloadConfig from '@/javascript/downloadConfiguration'
export default {

    methods:{
        closeMenu(){
            this.$emit('input',false);
        },

        logoutUser:function(){
            ConnectionHandler.ConnectionHandlerGet('/logout',null,()=>{
                this.$router.push({ name:'login'})
            });
        },
        
        downloadConfiguration:function(){
            DownloadConfig.exportConfiguration(this.getFormattedConfig);
        }

    }
}
</script>

<style lang="scss" scoped>
    @import '../scss/_simocTheme';

    .main__menu{
        @extend %display-panel;
        position:absolute;
        z-index: 2;
        left:0;
        right:0;
        top:0;
        bottom:0;
        margin: auto auto;
        height: 720px;
        width: 400px;
    }
    .menu__container{
        display: flex;
        flex-direction: column;        
        height: 100%;

        .title{
            width: 100%;
            font-size: 1.6rem;
            color: lightgreen;
            text-align: center;
            margin-bottom: 32px;
            font-family:'nasalization';
        }

        .title__container{
            display: flex;
        }

        .title__text{
            margin: auto auto;
        }

        .menu__close{
            color: white;
        }

        .menu__close:hover{
            color: #ea3b3b;
        }

        .title:after{
            width: 100%;
            display: block;
            margin: 4px 0px;
            border-bottom: 1px solid grey;
            content: '';
        }

        .form__submit__btn,.form__multi__btn{
            padding: 8px 16px;
            border: 2px solid #bb3b3b;
            border-radius: 10px;
            width: 60%;
            box-sizing: border-box;
            background-color: transparent;
            color: white;
            font-weight: 900;
            font-family:'nasalization';
            transition:ease-in-out .15s;
            align-self:center;
            margin-bottom: 32px;
        }

        .form__multi__btn{
            font-size: .75rem;
        }

        .form__submit__btn{
            font-size: 1.5rem;
        }

        .form__submit__btn:hover:not([disabled]),.form__multi__btn:hover:not([disabled]){
            background-color: #ea3b3b;
            border-color: #ea3b3b;
        }

        .form__submit__btn:disabled,.form__multi__btn:disabled{
            border: 2px solid grey;
            color: grey;
        }
        
        .form__submit__btn--logout{
            margin-top:auto;
        }
    }

    *:focus{
        outline: none;
    }  

</style>
