<template>
    <div class='chart-container'>
        <canvas :id='chartID' class='gauge' style='height: 100%; width: 100%;' height='auto' width='auto'></canvas>
    </div>
</template>
<script>
import Chart from 'chart.js';
export default {
    props:['chartID','chartBuild','chartColors'],

    data(){
        return{
            chart:'',
        }
    },

    computed:{
        updateChart:function(){
            let chartData = this.chartBuild.data;
            return chartData 
        }
    },

    watch:{
        updateChart: function(){
            this.chart.data.labels = [];
            this.chart.data.labels = this.chartBuild.labels;
            this.chart.data.datasets[0].data.pop()
            this.chart.data.datasets[0].data = this.updateChart
            this.chart.update()
        }
    },

    mounted: function(){
    const ctx = document.getElementById(this.chartID);
    this.chart = new Chart(ctx,{
      type: 'doughnut',
      data:{
        labels: this.getChartLabels,
        datasets:[{
          backgroundColor:this.chartColors,
          data:this.getChartData,
        }]
      },
      options:{
            percentageInnerCutout: 60,
            elements:{
                arc:{
                    borderWidth: 0
                }
            },
    
            legend:{
                onClick: null,
                display: true,
                fontColor: 'rgb(255, 255, 255)',
      
                position:'bottom',
                labels:{
                    fontColor:'white',
                    fontFamily:'roboto',
                    fontSize: 8,
                    boxWidth: 4,
                    usePointStyle: true,

                }
                
            },
            animation:{                
                animateScale: false,
                animateRotate: false,
            },
            defaultFontFamily:'Monospace',
            defaultFontStyle: 'normal',
            defaultFontColor:'rgb(255, 255, 255)', 
            responsive: true,
            maintainAspectRatio: false,
            drawborder: false,        
        },
    })  
  }
}
</script>

<style>    
    .chart-container{
        width:100%;
        height:100%;
        position:relative;
    }

    canvas{
        position:absolute;
        width:100%;
        height:100%;
    }
</style>
