<template>
    <div class='mission__stats'>
        <div class='line__item' v-for='(value,index) in plantData'>
            <div>Species:  {{value.species}}</div>
            
            <div>{{ageToHarvest(value.maxAge)}}</div>
        </div>
        <div></div>
        <div class='line__item'>
            <div>Solid Nitrogen:</div> <div>{{nutrients.sold_n}} kg</div>
        </div>
        <div class='line__item'>
            <div>Solid Phosphorus:</div> <div>{{nutrients.sold_p}} kg</div>
        </div>        
        <div class='line__item'>
            <div>Solid Potassium:</div> <div>{{nutrients.sold_k}} kg</div>
        </div>
        <div class='line__item'>
            <div>Solid Waste:</div> <div>{{nutrients.sold_wast}} kg</div>
        </div>
    </div>    
</template>

<script>
import {mapState,mapMutations,mapGetters} from 'vuex'
import ConnectionHandler from '@/javascript/connectionHandler'
export default {
    components:{

    },

    computed:{
        ...mapGetters({
            stepNumber:'getStepNumber',
            plantSpecies:'getPlantSpecies',
            nutrients:'getNutrientStorage',
        }),
        

    },

    methods:{
        ageToHarvest:function(maxAge){
            return (((this.stepNumber % (maxAge*24)) / (maxAge*24))*100).toFixed(2) + '%' // Need to fix
        }
    },

    data(){
        return{
            plantData:[]
        }
    },

    mounted:function(){
        this.plantSpecies.forEach(element => {
            if(element.species != '' && element.species != null){
                let plantAge = 0;
                let params =  {'agent_class':'plants','agent_name':element.species}
                
                ConnectionHandler.ConnectionHandlerGet('/get_agent_types',params,(response)=>{
                    console.log(response.data[0].char[1].value);
                    plantAge = response.data[0].char[1].value;
                    this.plantData.push({'species':element.species.toUpperCase(),'maxAge':plantAge});
                    console.log(this.plantData);
                })
            }
        })

    }
}
</script>

<style lang="scss" scoped>
    .mission__stats{
        color: white;
        display:grid;
        height: 100%;
        width: 100%;
        grid-template-rows: repeat(auto-fit, 1.5rem);
        grid-row-gap: 4px;

        .line__item{
            color: white;
            font-size: 1rem;
            font-family: 'roboto';
            display: grid;
            grid-template-columns: 1.5fr .75fr;
        }
    }
</style>
