<template>
    <div class='form'>
        <div class='form__input__container'>
            <label class='form__input form__username'>
                <fa-icon class='fa-icon' :icon="['fal','user']"/>
                <!--<div class='input__error'>Username Required</div>-->
                <input type='text' class='form__text input__text' v-model='username' maxlength='32' placeholder='ENTER USERNAME'>
            </label>
            <label class='form__input form__password'>
                <fa-icon class='fa-icon' :icon="['fal','key']"/>
                <!--<div class='input__error'>Password Required</div>-->
                <input type='password' class='form__text input__text' v-model='password' placeholder='PASSWORD'>
            </label>
        </div>
        <input type='button' value='LOGIN' class='form__submit__btn' v-on:click='loginUser()'>
        <div class='form__nav'>
            <router-link to='/register'>CREATE ACCOUNT</router-link>
            <router-link to=''>NEED HELP?</router-link>
        </div>
    </div>
</template>

<script>
import ConnectionHandler from '@/javascript/connectionHandler'
export default {
    props:['titleMessage'],

    data(){
        return{
            username:'',
            password:'',
        }
    },

    mounted: function(){
        this.$emit('input','LOGIN')
    },

    methods:{
        //loginBtn:function(){
        //    this.$router.push({ name:'register'})
        //}

        loginUser:function(){
            let params = {'username':this.username,'password':this.password};

            ConnectionHandler.ConnectionHandlerPost('/login',params, () =>{
                this.$router.push({ name:'menu'})
            });
        }
    }
}
</script>

<style lang="scss" scoped>
    @import '../scss/_simocTheme';
    .form{
        width: 100%;
        height: 100%;
        box-sizing:border-box;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        flex-direction: column;
    }
    .form__input__container{
        width:100%;
        height: auto;
        display:grid;
        grid-template-rows: repeat(auto-fit, auto);
        grid-row-gap: 64px;
    }

    .form__input{
        margin: auto auto;
        width: 100%;        
        box-sizing: border-box;
        display:grid;
        grid-template-columns: auto 1fr;
        grid-column-gap: 16px;
        position:relative;
        font-family:'Roboto';

        .fa-icon{
            font-size: 1.7rem;
            color: grey;
            margin: auto auto;
            box-sizing: border-box;
        }

        .form__text{
            position: absolute;
            left: 14%;
            bottom: 20%;
            width: 85%;
            background-color: transparent;
            border:none;
            color:white;
            font-size: 1.2rem;
        }

        .input__error{
            position: absolute;
            color: #bb3b3b;
            font-size: .8rem;
            bottom: -50%;
        }
    }

    .form__input::after{
        content:'';
        display:block;
        border-bottom:1px solid grey;
        margin-top: 4px;
        width: 100%;
        grid-column-start: span 2;
    }

    .form__nav{
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        font-family:'Roboto';
        font-size: .8rem;
        a{
            color:white;
        }
    }

    .form__submit__btn{
        margin-top: auto;
        margin-bottom: 32px;
        padding: 8px 16px;
        border: 2px solid #bb3b3b;
        border-radius:10px;
        width: 60%;
        box-sizing: border-box;
        background-color: transparent;
        color: white;
        font-size: 1.5rem;
        font-weight: 900;
        font-family:'nasalization';
        transition:ease-in-out .15s;
    }

    .form__submit__btn:hover{
        background-color: #ea3b3b;
        border-color: #ea3b3b;
    }

    *:focus{
        outline: none;
    }
</style>
