<template>
    <div class='panel__content'>
        <div class='title__bar'>
            <div class='title'>{{activeTitle}}</div>
            <panel-dropdown class='panel__dropdown' v-model='activeSelection' :options='panelOptions'/>
        </div>
        <component :is='activePanel'/>
    </div>
</template>

<script>
import PanelDropdown from '@/components/Panel_Dropdown';
import PanelEcosystemChart from '@/components/Panel_EcosystemChart';
import PanelEnergyChart from '@/components/Panel_Energy';
import PanelMission from '@/components/Panel_Mission';
import PanelAtmosphericComposition from '@/components/Panel_AtmosphericComposition';
import PanelConfiguration from '@/components/Panel_Configuration';
import PanelGreenhouse from '@/components/Panel_Greenhouse';

export default {    
    props:['initialPanel'],
    
    components:{
        'panel-dropdown':PanelDropdown,
        'panel-ecoystem-chart':PanelEcosystemChart,
        'panel-energy-chart':PanelEnergyChart,
        'panel-mission-stats':PanelMission,
        'panel-atmospheric-composition':PanelAtmosphericComposition,
        'panel-configuration':PanelConfiguration,
        'panel-greenhouse-stats':PanelGreenhouse,
    },

    mounted:function(){ 
        this.activeSelection = this.initialPanel;
    },

    computed:{
        activePanel:function(){
            return this.panelComponents[this.activeSelection].component;
        },

        activeTitle:function(){
            return this.panelComponents[this.activeSelection].title;
        }
    },

    data(){
        return{
            activeSelection:'configuration',
            
            panelComponents:{
                'configuration':{'title':'SESSION CONFIGURATION','component':'panel-configuration'},
                'ecosystemchart':{'title':'ATMOSPHERIC LEVELS','component':'panel-ecoystem-chart'},
                'energychart':{'title':'ENERGY','component':'panel-energy-chart'},
                'missionstats':{'title':'MISSION STATS','component':'panel-mission-stats'},
                'greenhousestats':{'title':'GREENHOUSE','component':'panel-greenhouse-stats'},
                //'plantstats':{'title':'PLANT SPECIES','component':'panel-plant-species'},
                'atmosphericcomposition':{'title':'ATMOSPHERIC COMPOSITION','component':'panel-atmospheric-composition'},
            },

            panelOptions:[
                {
                    'value':'configuration',
                    'key':'configuration',
                    'text':'CONFIGURATION'
                },
                {
                    'value':'ecosystemchart',
                    'key':'ecosystemgauges',
                    'text':'ATMOSPHERIC LEVELS'
                },
                {
                    'value':'energychart',
                    'key':'energychart',
                    'text':'ENERGY (CHART)'
                },
                {
                    'value':'missionstats',
                    'key':'missionstats',
                    'text':'MISSION STATS'
                },
                {
                    'value':'greenhousestats',
                    'key':'greenhousestats',
                    'text':'GREENHOUSE STATS'
                },
                /*{
                    'value':'plantstats',
                    'key':'plantstats',
                    'text':'PLANT SPECIES'
                },*/
                {
                    'value':'atmosphericcomposition',
                    'key':'atmosphericcomposition',
                    'text':'ATMOSPHERIC COMPOSITION'
                }
            ],
        }    
    }
}
</script>

<style lang='scss' scoped>
    @import '../scss/_simocTheme';
    
    .panel__content{        
        @extend %display-panel;
        display:grid;
        grid-template-columns:1fr;
        grid-template-rows: auto 1fr;
        grid-row-gap: 16px;
    }

    .title__bar{
        display: flex;
        justify-content: space-between;
        align-items:center;
        width: 100%;
        color: white;
        border-bottom: 1px solid grey;
        padding-bottom: 4px;
        font-size: 1rem;
        font-family:'nasalization';
        color: lightgreen;
    }

    .panel__dropdown{
        margin-left: auto;
    }

</style>
