<template>
    <div class='form__content'>
        <div class='title'>
            INITIAL SETTINGS
            <fa-icon class='icon' :icon="['fal','info-circle']" v-on:click='setSelectedWiki()'/>
        </div>
        <div class='section'>
            <input-select :callback="setBaselineModel" :fieldLabel="baselineModelLabel" :options="baselineModelOptions" v-model='baselineModel'/>
            <input-select :fieldLabel="locationLabel" :options="locationOptions" v-model='location' v-on:input='setLocation()'/>
            <div class='line__item--flex'>
                <input-number :callback='setDurationLength' :preset='presetDurationLength' :fieldLabel="durationLengthLabel"/>
                <input-select :callback='setDurationUnits' :preset='presetDurationUnits' :fieldLabel="durationUnitsLabel" :options="durationUnitOptions"/>
            </div>
        </div>
    </div>
</template>

<script>
import InputSelect from '@/components/Input_Select';
import InputNumber from '@/components/Input_Number';
import {mapState,mapMutations,mapGetters} from 'vuex'
export default {   
    components:{
        'input-select':InputSelect,
        'input-number':InputNumber,
    },

    data(){
        return{
            baselineModel:'',
            location:'',
            permanentStay:false,
            durationLength:0,
            durationUnits:'',

            baselineModelLabel:'PRESETS',
            locationLabel:'LOCATION',
            durationLengthLabel:'DURATION',
            durationUnitsLabel:'UNITS',

            baselineModelOptions:[
                {
                    'value':'custom',
                    'key':'custom',
                    'text':'Custom',
                },
                {
                    'value':'humansonly',
                    'key':'humansonly',
                    'text':'Humans Only',
                },
                {
                    'value':'hybridone',
                    'key':'hybridone',
                    'text':'Hybrid One',
                },
                {
                    'value':'hybridtwo',
                    'key':'hybridtwo',
                    'text':'Hybrid Two',
                },
                {
                    'value':'hybridthree',
                    'key':'hybridthree',
                    'text':'Hybrid Three',
                },
                {
                    'value':'hybridfour',
                    'key':'hybridfour',
                    'text':'Hybrid Four',
                },
                {
                    'value':'reset',
                    'key':'reset',
                    'text':'Reset Defaults',
                },


            ],

            locationOptions:[
                {
                    'value':'mars',
                    'key':'mars',
                    'text':'Mars'
                },
                {
                    'value':'moon',
                    'key':'moon',
                    'text':"Earth's Moon"
                },
            ],

            durationUnitOptions:[
                {
                    'value':'hour',
                    'key':'hours',
                    'text':'Hours'
                },
                {
                    'value':'day',
                    'key':'days',
                    'text':'Sols',
                },
                {
                    'value':'year',
                    'key':'years',
                    'text':'Years',
                }
            ]

        }
    },

    methods:{
        ...mapMutations([
            'SETBASELINEMODEL',
            'SETLOCATION',
            'SETDURATIONLENGTH',
            'SETDURATIONUNITS',
            'SETSELECTEDWIKI',
            'SETBASELINEPRESET',
        ]),

        setSelectedWiki:function(){
            this.SETSELECTEDWIKI('initial');
        },

        setBaselineModel:function(value){
            this.SETBASELINEMODEL(value);
            this.SETBASELINEPRESET(value);
        },

        setLocation:function(){
            this.SETLOCATION(this.location);
        },

        setDurationLength:function(value){
            this.SETDURATIONLENGTH(value);
        },

        setDurationUnits:function(value){
            this.SETDURATIONUNITS(value);
        }
    },

    computed:{
        ...mapGetters({
            presetDurationLength:'getDurationLength',
            presetDurationUnits:'getDurationUnits',
        })
    }
}
</script>

<style lang="scss" scoped>
    @import '../scss/_config_form';
        
    .form__content{
        @extend %form__content;

        .title{
            @extend %form__section__title;
        }

        .section{
            @extend %form__section;
        }

        .line__item--flex{
            @extend %form__line__item--flex;
        }
    }
</style>
