<template>
    <div class='form__content'>
        <div class='title'>
            GREENHOUSE
            <fa-icon class='icon'  :icon="['fal','info-circle']" v-on:click='setSelectedWiki()'/>
        </div>
        <div class='section'>
            <input-select :callback='setGreenhouse' :preset='presetGreenhouse' :fieldLabel="greenhouseLabel" :options="greenhouseOptions"/>
            <div class='line__item--flex' v-for='index in 3' :key='index'>
                <input-select-plant :index="(index-1)" :callback='setPlantSpecies' :preset='presetPlantSpecies[index-1].species' :fieldLabel="plantSpeciesLabel" :options="plantSpeciesOptions"/>
                <input-number :index="(index-1)" :callback='setPlantAmount' :preset='presetPlantSpecies[index-1].amount' :fieldLabel="plantamountLabel"/>
                <input-select-plant :index="(index+2)" :callback='setPlantSpecies' :preset='presetPlantSpecies[index+2].species' :fieldLabel="plantSpeciesLabel" :options="plantSpeciesOptions"/>
                <input-number :index="(index+2)" :callback='setPlantAmount' :preset='presetPlantSpecies[index+2].amount' :fieldLabel="plantamountLabel"/>
            </div>
        </div>
    </div>
</template>

<script>
import InputSelect from '@/components/Input_Select';
import InputSelectPlant from '@/components/Input_Select_Plant';
import InputNumberPlant from '@/components/Input_Number_Plant';
import {mapState,mapMutations,mapGetters} from 'vuex'
import ConnectionHandler from '@/javascript/connectionHandler'
export default {   
    components:{
        'input-select':InputSelect,
        'input-select-plant':InputSelectPlant,
        'input-number':InputNumberPlant,
    },

    data(){
        return{
            greenhouse:'',
            plantSpecies:[
                {'species':'','amount':0},
                {'species':'','amount':0},
                {'species':'','amount':0},
                {'species':'','amount':0},
                {'species':'','amount':0},
                {'species':'','amount':0},
            ],

            greenhouseLabel:'GREENHOUSE',
            plantSpeciesLabel:'SPECIES',
            plantamountLabel:'SQR METERS',
            
            greenhouseOptions:[
                {
                    'value':'none',
                    'key':'none',
                    'text':'None'
                },
                {
                    'value':'small',
                    'key':'small',
                    'text':'Small - Greenhouse'
                },
                {
                    'value':'medium',
                    'key':'medium',
                    'text':'Medium - Greenhouse'
                },
                {
                    'value':'large',
                    'key':'large',
                    'text':'Large - Greenhouse'
                },
            ],

            plantSpeciesOptions:[
                {
                    'value':'none',
                    'key':'none',
                    'text':'NONE',
                },
            ]
        }
    },

    methods:{
        ...mapMutations([
            'SETGREENHOUSE',
            'SETPLANTSPECIES',
            'SETPLANTAMOUNT',
            'SETSELECTEDWIKI',
        ]),

        setSelectedWiki:function(){
            this.SETSELECTEDWIKI('greenhouse');
        },

        setGreenhouse:function(value){
            this.SETGREENHOUSE(value);
        },

        setPlantSpecies:function(index,value){
            console.log(index);
            let species = {
                'index':index,
                'species':value
            }

            if(species.species != null && species.species !== '' && species.species !== 'none'){
                this.SETPLANTSPECIES(species);
            }
        },

        setPlantAmount:function(index,value){
            console.log(value);
            let species = {
                'index':index,
                'amount':value
            }
            
            if(species.amount == null || species.amount === ''){
                species.amount = 0;
            }

            this.SETPLANTAMOUNT(species);
        }
    },

    mounted:function(){
        let params = {'agent_class':'plants'};

        ConnectionHandler.ConnectionHandlerGet('/get_agent_types',params,(response)=>{
            response.data.forEach(element =>{
                let formatted = String(element.name).toUpperCase();
                formatted = formatted.replace('_',' ');
                this.plantSpeciesOptions.push({
                    'value':String(element.name),
                    'key':String(element.name),                        
                    'text':formatted
                });
            });
            
            
            
        });
    },


    computed: {
        ...mapGetters({
            remainingSpace:'calcGreenhouseRemaining',
            presetGreenhouse:'getGreenhouse',
            presetPlantSpecies:'getPlantSpecies',
        }),
    }
}
</script>

<style lang="scss" scoped>
    @import '../scss/_config_form';
        
    .form__content{
        @extend %form__content;

        .title{
            @extend %form__section__title;
            
        }

        .section{
            @extend %form__section;
        }

        .line__item--flex{
            @extend %form__line__item--flex;
        }
    }
</style>
