<template>
    <div class='form__content'>
        <div class='title'>
            INHABITANTS
            <fa-icon class='icon' :icon="['fal','info-circle']" v-on:click='setSelectedWiki()'/>
        </div>
        <div class='section'>
            <input-number :callback='setHumans' :preset='presetHumans' :fieldLabel="'HUMANS'" :setValue="humans" v-model='humans' v-on:input='setHumans()'/>
            <input-number :callback='setFood' :preset='presetFood' :fieldLabel="'FOOD (kg)'" v-model='food' v-on:input='setFood()'/>
            <input-select :callback='setCrewQuarters' :preset='presetCrewQuarters' :fieldLabel="'CREW QUARTERS'" v-model='crewQuarters' v-on:input='setCrewQuarters()' :options="crewQuartersOptions"/>
            <input-number :callback='setECLSS' :preset='presetECLSS' :fieldLabel="'ECLSS'" v-model='eclss' v-on:input='setECLSS()'/>
        </div>
    </div>
</template>

<script>
import InputSelect from '@/components/Input_Select';
import InputNumber from '@/components/Input_Number';
import {mapState,mapMutations,mapGetters} from 'vuex'
export default {   
    components:{
        'input-select':InputSelect,
        'input-number':InputNumber,
    },

    data(){
        return{
            humans:0,
            food:0,
            crewQuarters:0,
            eclss:0,

            crewQuartersOptions:[
                {
                    'value':'none',
                    'key':'none',
                    'text':'None'
                },
                {
                    'value':'small',
                    'key':'small',
                    'text':'Small - Crew Quarters'
                },
                {
                    'value':'medium',
                    'key':'medium',
                    'text':'Medium - Crew Quarters'
                },
                {
                    'value':'large',
                    'key':'large',
                    'text':'Large - Crew Quarters'
                },
            ],
        }
    },

    methods:{
        ...mapMutations([
            'SETHUMANS',
            'SETECLSS',
            'SETFOOD',
            'SETCREWQUARTERS',
            'SETSELECTEDWIKI',
        ]),

        setSelectedWiki:function(){
            this.SETSELECTEDWIKI('inhabitants');
        },

        setHumans:function(value){
            this.SETHUMANS(value);
        },
        
        setFood:function(value){
            this.SETFOOD(value);
        },

        setECLSS:function(value){
            this.SETECLSS(value);
        },

        setCrewQuarters:function(value){
            this.SETCREWQUARTERS(value);
        }
    },

    computed:{
        ...mapGetters({
            presetHumans:'getHumans',
            presetFood:'getFood',
            presetCrewQuarters:'getCrewQuarters',
            presetECLSS:'getECLSS',
        })
    }
}
</script>

<style lang="scss" scoped>
    @import '../scss/_config_form';
        
    .form__content{
        @extend %form__content;

        .title{
            @extend %form__section__title;
            margin-bottom: 16px;
        }

        .section{
            @extend %form__section;
        }
    }
</style>
