<template>
  <div class='form__select'>
    <select ref='selectField'  v-model='selectValue' @input='updateValue()'>
      <option class='select__option' :value='option.value' v-for='option in options' :key='option.key'>
          {{option.text}}
      </option>
    </select>
  </div>
</template>

<script>
export default{
  props:['options'],

  data(){
    return{
      selectValue:'',
    }
  },

  methods:{
    updateValue(){
      this.$emit('input',this.$refs.selectField.value);
    }
  }
}
</script>

<style lang='scss' scoped>
    .form__select{
      position:relative;
      margin: 4px 4px;
      width: fit-content;
      box-sizing: border-box;
    }

    label{
      position:absolute;
      color: white;
      font-size: .6rem;
      background-color: #000;
      top:-15%;
      left: 7%;
      font-family: 'nasalization';
      color: #00aaee;
      z-index: 1;
    }

    select{
      width: 24px;
      height: 24px;
      border: none;
      -webkit-appearance: none;
      -moz-appearance: none;
      appearance: none;
      background: url('../assets/dots-horizontal.png') no-repeat;
      background-size: 24px 24px;
      background-position: right center;
      color: transparent;
      font-family:'roboto';
      font-size: .8rem;
      text-indent: 0.01px;
      text-overflow: '';
    }

    option{
      color: black;
      padding: 0px 4px;
    }

    *:focus{
      outline: none;
    }

</style>