<template>
    <div class='helper__container'>
        <div class='title__bar'>ENERGY ALLOCATION</div>
        <div class='helper__energy' style="color:white; font-family:'nasalization'">
            Chart Coming Soon!         
        </div>   
    </div>
</template>

<script>
import {mapState,mapMutations,mapGetters} from 'vuex'
import ChartPie from '@/components/Chart-Greenhouse'
import ConnectionHandler from '@/javascript/connectionHandler'
export default {

    components:{
        'chart-pie':ChartPie,
    
    },

    data(){
        return{
            energyProduced:0,
            energyConsumed:0,
        }
    },

    computed:{
        ...mapGetters({
            solarArrayQuantity:'getSolarArrays',
            powerStorageQuantity:'getPowerStorage',
            eclssQuantity:'getECLSS',
            structures:'getStructureAgents',
            greenhouseQuantity:'getGreenhouseAgent',
            crewQuartersQuantity:'getCrewQuartersAgent',

        }),

        energyProduction:function(){
            let params = {'agent_name':'solar_pv_array_mars','quantity':this.solarArrayQuantity}
            ConnectionHandler.ConnectionHandlerGet('/get_energy',params,(response)=>{
                this.energyProduced = response.data;    
            })

            return this.energyProduced;
        }
    },

    watch:{
        solarArrayQuantity(){
            let params = {'agent_name':'solar_pv_array_mars','quantity':this.solarArrayQuantity}
            ConnectionHandler.ConnectionHandlerGet('/get_energy',params,(response)=>{
                this.energyProduced = response.data;    
            })
        },

        powerStorageQuantity(){
            
        },

        eclssQuantity(){
            let params = {'agent_name':'eclss','quantity':this.eclssQuantity}
            ConnectionHandler.ConnectionHandlerGet('/get_energy',params,(response)=>{
                this.energyConsumed += response.data;
            })
        },

        greenhouseQuantity(){
            let params = {'agent_name':this.greenhouseQuantity,'quantity':1}
            ConnectionHandler.ConnectionHandlerGet('/get_energy',params,(response)=>{
                this.energyConsumed = response.data;
            })
        },

        crewQuartersQuantity(){
            let params = {'agent_name':this.crewQuartersQuantity,'quantity':1}
            ConnectionHandler.ConnectionHandlerGet('/get_energy',params,(response)=>{
                this.energyConsumed = response.data;
            })
        }


    }


}
</script>

<style lang="scss" scoped>
    @import '../scss/_simocTheme';
    .helper__energy{
        height: 100%;
    }

    .helper__container{
        @extend %display-panel;
        display:grid;
        grid-template-columns: 1fr;
        grid-template-rows: auto 1fr;
        grid-row-gap: 16px;
    }

    .title__bar{
        display: flex;
        justify-content: space-between;
        align-items:center;
        width: 100%;
        color: white;
        border-bottom: 1px solid grey;
        padding-bottom: 4px;
        font-size: .75rem;
        font-family:'nasalization';
        color: lightgreen;
    }
    
</style>

