<template>
    <div class='chart-container'>
        <canvas :id='chartID' class='gauge' style='height: 100%; width: 100%;' height='auto' width='auto'></canvas>
        <!--<div class='chart__center'>{{chartCenter}}</div>-->
    </div>
</template>
<script>
import Chart from 'chart.js';
export default {
    props:['chartID','chartBuild','chartColors','chartCenter'],

    data(){
        return{
            chart:'',
        }
    },

    computed:{
        updateChart:function(){
            let chartData = this.chartBuild.data;
            return chartData; 
        },
    },

    watch:{
        updateChart: function(){
            this.chart.options.elements.centerText.text = this.chartCenter;
            this.chart.data.labels = [];
            this.chart.data.labels = this.chartBuild.labels;
            this.chart.data.datasets[0].data.pop()
            this.chart.data.datasets[0].data = this.updateChart

            let chartColors = this.chartBuild.colors;
            let chartRanges = this.chartBuild.ranges;
            let data = this.chartBuild.data[0];
            
            this.chart.data.datasets[0].backgroundColor = ['#00aaee','#fff'];

            let color = String(chartColors[chartColors.length-1]);
            
            
            this.chartBuild.ranges.forEach( function(element,i){
                if(parseFloat(data) <= parseFloat(element)){    
                    color = String(chartColors[i]);
                }
            })
                        
            this.chart.data.datasets[0].backgroundColor = [color,'#fff'];
            this.chart.update()
        },

    },



    mounted: function(){
        const ctx = document.getElementById(this.chartID);
        this.chart = new Chart(ctx,{
            type: 'doughnut',
            data:{
                centerText: this.centerText,
                labels: this.getChartLabels,
                datasets:[{
                    backgroundColor:this.chartColors,
                    data:this.getChartData,
                }]
            },
            rotation: Math.PI * -.5,
            options:{
                elements:{
                    arc:{
                        borderWidth: 0
                    },
                    centerText:{
                        text:this.chartCenter,
                    }
                },
        
                legend:{
                    display: false,
                    fontColor: 'rgb(255, 255, 255)',
        
                    position:'right',
                    labels:{
                        fontColor:'white',
                        fontFamily:'roboto',
                        fontSize: 8,
                        boxWidth: 4,
                        usePointStyle: true,

                    }
                    
                },
                animation:{                
                    animateScale: false,
                    animateRotate: false,
                },

                defaultFontFamily:'Monospace',
                defaultFontStyle: 'normal',
                defaultFontColor:'rgb(255, 255, 255)', 
                responsive: true,
                maintainAspectRatio: false,
                drawborder: false,
                cutoutPercentage: 70,
                rotation: Math.PI,
                circumference: 1 * Math.PI,        
            },
            
            plugins:[{
                beforeDraw: function(chart){
                    var width = chart.chart.width;
                    var height = chart.chart.height;
                    var ctx = chart.chart.ctx;

                    ctx.restore();
                    var fontSize = (height/64).toFixed(2);
                    ctx.font = fontSize + "em sans-serif";
                    ctx.textBaseline = "bottom";
                    

                  

                    var text = chart.chart.options.elements.centerText.text,
                        textX = Math.round((width - ctx.measureText(text).width) / 2),
                        textY = height;


                    ctx.fillStyle = 'white';
                    ctx.fillText(text,textX,textY);
                    ctx.save();
                }
            }]
        })
    },
}
</script>

<style>    
    .chart-container{
        width:100%;
        height:100%;
        position:relative;
    }

    .chart__center{
        position: absolute;
        width: 100%;
        text-align:center;
        bottom: 0;
        font-size: 1.2rem;
        font-family:'roboto'
    }

    canvas{
        position:absolute;
        width:100%;
        height:100%;
    }
</style>
