import store from '../store'

import axios from 'axios'
axios.defaults.withCredentials = true;
require('promise.prototype.finally');

var stepSize = 1;
var updateInterval = 1000;
var updateRunning = true;
var terminated = false;
var currentStep = null;
var updateIntervalID = null;
var stepPerSecond = 1;

var stepString = '';

const localHost = 'http://localhost:8000'

function GetStepUpdate(){
    const PATH = '/get_step';

    if( currentStep !== null && updateRunning && !terminated){
        currentStep += stepSize;
        currentStep = parseInt(currentStep);
    }

    let params = {'step_num':currentStep};
    axios.get(localHost + PATH, { step_num:currentStep} 
    ).then(response =>{
        if(response.status == 200){
            console.log(response.data);
            let agentTypes = response.data.agents.total_agent_types;
            let consumption = response.data.agents.total_consumption;
            let production = response.data.agents.total_production;
            let modelStats = response.data.model_stats;
            var nurtientStats = null;
            stepString = response.data;
            response.data.storages.forEach(element=>{
                //console.log(element.agent_type);
                if(element.agent_type === 'nutrient_storage'){
                    nurtientStats = element.currencies;
                }
            })     

            terminated = response.data.is_terminated;
            store.dispatch('UpdateStepData',{
                'stepNumber':response.data.step,
                
                'humans':agentTypes.human_agent,
        
                'co2Ratio':modelStats.air_storage_1.atmo_co2_ratio, 
                'co2Produced':production.atmo_co2,
                'co2Consumed':consumption.atmo_co2,
                
                'o2Ratio':modelStats.air_storage_1.atmo_o2_ratio,
                'o2Produced':production.atmo_o2,
                'o2Consumed':consumption.atmo_o2,
        
                'h2oAtmo':modelStats.air_storage_1.atmo_h2o_ratio,
                'h2oRatio':modelStats.water_storage_2.atmo_h2o_ratio,
                'h2oPotableProduced':production.h2o_potb,
                'h2oPotableConsumed':consumption.h2o_potb,
        
                'h2oWasteRatio':modelStats.water_storage_2.h2o_wste_ratio,
                'h2oWasteProduced':production.h2o_wste,
                'h2oWasteConsumed':production.h2o_wste,
        

                'ch4Ratio':modelStats.air_storage_1.atmo_ch4_ratio,
                'h2Ratio':modelStats.air_storage_1.atmo_h2_ratio,
                'n2Ratio':modelStats.air_storage_1.atmo_n2_ratio,

                'kwhEnergyProduced':production.enrg_kwh,
                'kwhEnergyConsumed':consumption.enrg_kwh,

                'nutrients':nurtientStats,

                'jsonString':response.data,
            })
        }
    }).catch(error =>{
        console.log(error.response.status);
    }).finally(()=>{
        SetUpdateInterval();
    })
}

function SetUpdateInterval(){
    updateIntervalID = setTimeout(function(){
        if(updateRunning && !terminated){
            GetStepUpdate();
        }
    },updateInterval);
}

function PauseUpdateInterval(){
    updateRunning = !updateRunning;
    if(updateRunning){
        SetUpdateInterval();
    } else if(!updateRunning){
        clearTimeout(updateIntervalID);
    }

    return updateRunning;
    //return the updateRunning state to switch the graphics.
}

function PauseFromMenu(){
    updateRunning = false;
    clearTimeout(updateIntervalID);
}

function RestartFromMenu(){
    updateRunning = true;
    SetUpdateInterval();
}

function IncreaseUpdateSpeed(){
    stepPerSecond = Math.min(8,(stepPerSecond*2));
    updateInterval = Math.max(125,(1000/stepPerSecond));
}

function DecreaseUpdateSpeed(){
    stepPerSecond = Math.max(1,(stepPerSecond/2));
    updateInterval = Math.min(1000,(1000/stepPerSecond));
}

function GetStepRate(){
    return stepPerSecond;
}

function GetStepData(){
    return stepString;
}

function ClearUpdateInterval(){
    updateRunning = false;
    clearTimeout(updateIntervalID);
    stepPerSecond = 1;
    updateInterval = 1000;
    updateRunning = true;
    currentStep = null;
}

export default{PauseFromMenu,RestartFromMenu,ClearUpdateInterval,GetStepRate,GetStepUpdate,PauseUpdateInterval,IncreaseUpdateSpeed,DecreaseUpdateSpeed,GetStepData}