<template>
    <div class='toolbar'>
        <div class='toolbar__title'>
            SESSION CONFIGURATION
        </div>
        <div class='toolbar__link disabled' disabled>
            <fa-icon class='fa-icon' :icon="['fal','upload']"/>
            <div class='link__item'>UPLOAD CONFIGURATION</div>
        </div>
        <div class='toolbar__link' v-on:click='downloadConfiguration'>
            <fa-icon class='fa-icon' :icon="['fal','download']"/>
            <div class='link__item'>DOWNLOAD CONFIGURATION</div>
        </div>
        <div class='toolbar__link disabled' disabled>
            <fa-icon class='fa-icon' :icon="['fal','save']"/>
            <div class='link__item'>SAVE CONFIGURATION</div>
        </div>
        <div class='toolbar__main' v-on:click='openMenu()'>
            <fa-icon class='fa-icon' :icon="['fal','bars']"/>
            <div class='link__item'>MAIN MENU</div>
        </div>
    </div>
</template>

<script>
import DownloadConfig from '@/javascript/downloadConfiguration'
import {mapState,mapMutations,mapGetters} from 'vuex'
export default {
    methods:{
        openMenu(){
            this.$emit('input',true);
        },

        downloadConfiguration:function(){
            DownloadConfig.exportConfiguration(this.getFormattedConfig);
        }
    },

    computed:{
        ...mapGetters({
            getFormattedConfig:'getFormattedConfig'
        })
    }
}
</script>

<style lang="scss" scoped>
    @import '../scss/_simocTheme';
    .toolbar{
        @extend %display-panel;
        display:flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;

        .toolbar__link{
            margin-bottom: 32px;
        }
    }

    .toolbar__title{
        font-size: 1.2rem;
        font-weight: 900;
        font-family:'nasalization';
        text-align: center;
        color: white;
        margin-bottom: 32px;
    }

    .toolbar__title::after{
        display:block;
        margin: 8px 0px;
        border-bottom: 1px solid grey;
        content:'';
    }

    .toolbar__link,.toolbar__main{
        width: 100%;
        display: grid;
        grid-template-columns: auto 1fr;
        grid-template-rows: repeat(auto-fit,auto);
        grid-column-gap: 16px;
        height: auto;
        font-size: .8rem;
        font-weight: 900;
        font-family:'nasalization';
        color: white;
        box-sizing: border-box;
        
        .link__item{
            margin: auto 0px;
        }

        .fa-icon{
            font-size: 1.5rem;
            margin: auto auto;
        }
    }

    .toolbar__link:hover:not([disabled]),.toolbar__main:hover:not([disabled]){
        color: #00aaee;
        cursor: pointer;
    }

    .toolbar__main{
        margin-top: auto;
        font-size: 1rem;
    }

    .toolbar__main::before{
        display:block;
        content:'';
        border-top: 1px solid grey;
        margin: 16px 0px;
        width: 100%;
        grid-column: span 2;
    }

    .disabled{
        color: grey;
    }

</style>
