<template>
    <div class='wiki__container'>
        <div class='wiki__nav'>
            <div class='wiki__nav__title'>
                SECTIONS
            </div>
            <div class='wiki__nav__item' v-for='(value,index) in wiki.navItems' v-on:click='setSection(value.value)'>
                {{value.link}}
            </div>
        </div>
        <div class='wiki'>
            <div class='wiki__content__title'>
                {{wiki.title}}
            </div>
            <div class='wiki__content' v-html='wikiContent'>

            </div>
        </div>
    </div>
</template>

<script>
import {mapState,mapMutations,mapGetters} from 'vuex'
export default {

    data(){
        return{
            section: 'welcome',
        }
    },

    computed:{
        ...mapGetters({
            wiki:'getWikiAttributes',
        }),

        wikiContent:function(){
            return this.wiki.content[this.section]; 
        }
    },

    methods:{
        setSection:function(str){
            this.section = str;
        }
    },

    watch:{
        wiki:function(){
            this.section = 'welcome';
        }
    }


}
</script>

<style lang="scss" scoped>
    @import '../scss/_simocTheme';
    .wiki__container{
        @extend %display-panel;
        width: 100%;
        grid-column: span 2;
        display: grid;
        grid-template-columns: minmax(160px,1fr) 2fr;

        color: white;
        font-family:'roboto';

        .wiki__nav{
            display: flex;
            flex-direction:column;
            justify-content: flex-start;
            align-items: center;
            border-right: 1px solid grey;
            padding-right: 16px;
            

            .wiki__nav__title{
                color: lightgreen;
                text-align:center;
                font-size: 1.2rem;
                font-family: 'nasalization';
                margin-bottom: 16px;
            }

            .wiki__nav__title:after{
                content:"";
                display: block;
                margin: 4px 0px;
                border-bottom: 1px solid grey;
            }

            .wiki__nav__item{
                font-size: 1.2rem;
                font-weight: 900;
                font-family: 'nasalization';
                margin-bottom: 16px;
                text-transform:capitalize;
            }

            .wiki__nav__item:hover{
                color: #00aaee;
                text-decoration: underline;
                cursor: pointer;
            }
        }        

        .wiki{
            padding-left: 16px;
            width: 100%;
            box-sizing:border-box;

            .wiki__content__title{
                width: fit-content;
                margin: auto auto;
                color: lightgreen;
                text-align:center;
                font-size: 1.2rem;
                font-family:'nasalization';
                margin-bottom:16px;
                
            }

            .wiki__content__title:after{
                content:"";
                display:block;
                margin: 4px 0px;
                border-bottom: 1px solid grey;
            }

            .wiki__content{
                font-size: 1rem;
                font-weight:100;
                line-height: 1.7rem;
            }
        }
    }

    p{
        margin-bottom:1rem;
    }

</style>
