export default{
    state:{
        stepNumber: 0,
        humans:0,

        co2Ratio:0,
        co2Consumed:0,
        co2Produced:0,

        o2Ratio:0,
        o2Consumed:0,
        o2Produced:0,

        food:0,

        h2oAtmo:0,
        h2oRatio:0,
        h2oPotableConsumed:0,
        h2oPotableProduced:0,
        h2oWasteRatio:0,

        kwhEnergyProduced:0,
        kwhEnergyConsumed:0,

        ch4Ratio:0,
        n2Ratio:0,
        h2Ratio:0,

        nutrients:{'sold_n':0,'sold_p':0,'sold_k':0,'sold_wast':0},

        jsonString:'',
    },

    getters:{
        getEcosystem: state => {
            let data = {
                'o2Ratio':state.o2Ratio,
                'co2Ratio':state.co2Ratio,
                'h2oRatio':state.h2oAtmo,
                'ch4Ratio':state.ch4Ratio,
                'n2Ratio':state.n2Ratio,
                'h2Ratio':state.h2Ratio,
            }
            return data;
        },

        getClock: state=>{
            let data ={
                'stepNumber':state.stepNumber,
                'solNumber': parseInt(state.stepNumber / 24.667),
            }

            return data;
        },

        getMission: state =>{
            let data = {
                'humans':state.humans,
                'stepNumber':state.stepNumber,
            }

            return data;
        },

        getEnergy: state =>{
            let data = {
                'kwhEnergyProduced':state.kwhEnergyProduced.value,
                'kwhEnergyConsumed':state.kwhEnergyConsumed.value,
            }
            
            return data;
        },

        getStepNumber: state=>{
            return state.stepNumber;
        },

        getNutrientStorage: state =>{
            return state.nutrients;
        },

        getJSONString: state =>{
            return state.jsonString;
        }
    },

    mutations:{


        SETSTEPNUMBER(state,value){
            state.stepNumber = value;
        },
        
        SETHUMANS(state,value){
            state.humans = value;
        },
        
        SETCO2RATIO(state,value){
            state.co2Ratio = value;
        },
        SETCO2CONSUMED(state,value){
            state.co2Consumed = value;
        },
        SETCO2PRODUCED(state,value){
            state.co2Produced = value;
        },

        SETO2RATIO(state,value){
            state.o2Ratio = value;
        },
        SETO2CONSUMED(state,value){
            state.o2Consumed = value;
        },
        SETO2PRODUCED(state,value){
            state.o2Produced = value;
        },

        SETFOOD(state,value){
            state.food = value;
        },

        SETH20RATIO(state,value){
            state.h2oRatio = value;
        },
        SETH2OPOTABLECONSUMED(state,value){
            state.h2oPotableConsumed = value;
        },
        SETH2OPOTABLEPRODUCED(state,value){
            state.h2oPotableProduced = value;
        },
        SETH2OWASTERATIO(state,value){
            state.h2oWasteRatio = value;
        },

        SETKWHENERGYPRODUCED(state,value){
            state.kwhEnergyProduced = value;
        },
        SETKWHENERGYCONSUMED(state,value){
            state.kwhEnergyConsumed = value;
        },

        UPDATESTEPDATA(state,value){
            
            state.stepNumber = value.stepNumber;
            state.humans = value.humans;
            
            state.co2Ratio = value.co2Ratio;
            state.co2Consumed = value.co2Consumed;
            state.co2Produced = value.co2Produced;

            state.o2Ratio = value.o2Ratio;
            state.o2Consumed = value.o2Consumed;
            state.o2Produced = value.o2Produced;

            state.h2oAtmo = value.h2oAtmo;
            state.h2oRatio = value.h2oRatio;
            state.h2oPotableConsumed = value.h2oPotableConsumed;
            state.h2oPotableProduced = value.h2oPotableProduced;
            state.h2oWasteRatio = value.h2oWasteRatio;

            state.kwhEnergyConsumed = value.kwhEnergyConsumed;
            state.kwhEnergyProduced = value.kwhEnergyProduced;
            
            state.ch4Ratio= value.ch4Ratio;
            state.h2Ratio = value.h2Ratio;
            state.n2Ratio = value.n2Ratio;

            value.nutrients.forEach(element=>{
                switch(element.name){
                    case 'sold_k':
                        state.nutrients.sold_k = element.value;
                        break;
                    case 'sold_p':
                        state.nutrients.sold_p = element.value;
                        break;
                    case 'sold_n':
                        state.nutrients.sold_n = element.value;
                        break;
                    case 'sold_wast':
                        state.nutrients.sold_wast = element.value;
                        break;
                }
            })

            state.jsonString = value.jsonString;
        }   
    },

    actions:{
        UpdateStepData(context,value){
            context.commit('UPDATESTEPDATA',value);
        }

    }

}