<template>
    <div class='form__integer'>
        <label>{{fieldLabel}}</label>
        <button class='wrapper wrapper--icon' v-on:click='decrementValue()'>
            <fa-icon class='icon' :icon="['fal','minus']"/>
        </button>
        <div class='wrapper wrapper--input'>
            <input type='number' ref='numberField' value=0 v-model='fieldValue' @input='updateValue()' class='input__number'> <!-- change v-model and all related to the variable passed in. -->
        </div>
        <button class='wrapper wrapper--icon' v-on:click='incrementValue()'>
            <fa-icon class='icon' :icon="['fal','plus']"/>
        </button>
    </div>
</template>

<script>
export default {
    props:['fieldLabel','preset','callback'],

    data(){
        return{
            fieldValue:0,
        }
    },

    methods:{
        updateValue(){
            this.callback(this.fieldValue);
            //this.$emit('input',this.fieldValue);
        },

        incrementValue(){
            if(this.fieldValue == null || this.fieldValue === ''){
                this.fieldValue = 0;
            }
            this.fieldValue = Math.max(0,parseInt(this.fieldValue)+1);
            this.updateValue();
        },

        
        decrementValue(){
            if(this.fieldValue == null || this.fieldValue === ''){
                this.fieldValue = 0;
            }

            this.fieldValue = Math.max(0,parseInt(this.fieldValue)-1);
            this.updateValue();
        }
    },

    watch:{
        preset:function(){
            this.fieldValue = this.preset;
        }
    },
}
</script>

<style lang='scss' scoped>
    .form__integer{
        width: fit-content;
        display: grid;
        grid-template-columns: 24px auto 24px;
        grid-template-rows: 24px;
        position:relative;     
    
        label{
            width: fit-content;
            position: absolute;
            left: 50%;
            top:-15%;
            color: white;
            font-size: .6rem;
            background-color: #000;
            font-family:'nasalization';
            color: #00aaee;
            z-index: 1;
            transform: translate(-50%,0%);
        }

        .input__number{
            width: 6rem;
            text-align: center;
            background-color: transparent;
            color: white;
            border:none;
            -moz-appearance: textfield;
            font-size: .8rem;   

            &::-webkit-inner-spin-button,
            &::-webkit-outer-spin-button{
                -webkit-appearance: none;
                margin: 0;
            }

            &:focus{
                outline:none;
            }
        }


    }

    .wrapper{
        border:1px solid grey;
        box-sizing: border-box;
        display: flex;
        justify-content: center;
        align-items:center;
    }

    .wrapper--input{
        border-left: none;
        border-right: none;

        input{
            margin-top:auto;
        }
    }

    .wrapper--icon{
        background-color: white;
        color: black;
    }

    
</style>
