        <div class='title'>LIVE AGENT FEED</div>
        <div class='text'>{{liveData}}</div>
    </div>
</template>

<script>
import PanelBase from '@/components/Panel_Base'
import StepUpdateInterval from '@/javascript/stepUpdateInterval'
import {mapState,mapMutations,mapGetters} from 'vuex'
export default {
    data(){
        return{
            liveData:'',
        }
    },

    components:{
        'panel-base':PanelBase,
    },

    computed:{
        ...mapGetters({
            stepData:'getJSONString',
        }),
    },

    watch:{
        stepData:function(){
            this.liveData = this.stepData;
        }
    }
}
</script>

