<template>
  <div class='form__select'>
    <label>{{fieldLabel}}</label>
    <select ref='selectField' @input='updateValue()'>
      <option class='select__option' :value='option.value' v-for='option in options' :key='option.key' :selected='option.value == preset'>
          {{option.text}}
      </option>
    </select>
  </div>
</template>

<script>
export default{
  props:['options','fieldLabel','preset','callback','index'],

  data(){
    return{
      selectValue:'', //bind select to :value='' store value to get preconfigs
    }
  },

  methods:{
    updateValue(){
      console.log(this.index);
      this.callback(this.index,this.$refs.selectField.value);
      //this.$emit('input',this.$refs.selectField.value);
    }
  }
}
</script>

<style lang='scss' scoped>
    .form__select{
      position:relative;
      margin: 4px 4px;
      width: fit-content;
      box-sizing: border-box;
    }

    label{
      position:absolute;
      color: white;
      font-size: .6rem;
      background-color: #000;
      top:-15%;
      left: 7%;
      font-family: 'nasalization';
      color: #00aaee;
      z-index: 1;
    }

    select{
      border: 1px solid grey;
      -webkit-appearance: none;
      -moz-appearance: none;
      appearance: none;
      background: url('../assets/chevron-down-light.png') no-repeat;
      background-size: 8px 8px;
      background-position: right 8px center;
      color: white;
      font-family:'roboto';
      font-size: .7rem;
      text-indent: 0.01px;
      text-overflow: '';
      padding: 8px 18px 2px 8px;
    
      option{
        color: black;
      }
    }

</style>