<template>
    <div class='ecosystem-chart-content' style='color:white'>
        <div class='chart__container'>
             <chart-gauge :chartID="'e1'" :chartBuild="{'colors':['#ff0000','#ff9900','#FFFF00','#567D46'],'ranges':[12,15,19.5],'labels':['O2 RATIO',''],'data':[panelData.o2Ratio.toFixed(5)*100,o2Remainder*100]}" :chartCenter="(panelData.o2Ratio*100).toFixed(3)+'%'"/>
            <div class='chart__title'>O<sub>2</sub> (0-100%)</div>
        </div>
        <div class='chart__container'>
            <chart-gauge :chartID="'e2'" :chartBuild="{'colors':['#567D46','#FFFF00','#ff9900','#ff0000'],'ranges':[.06,.1,.25],'labels':['CO2 RATIO',''],'data':[panelData.co2Ratio.toFixed(5)*100,co2Remainder*100]}" :chartCenter="(panelData.co2Ratio*100).toFixed(3)+'%'"/>
            <div class='chart__title'>CO<sub>2</sub> (0-3%)</div>
        </div>
        <div class='chart__container'>
            <chart-gauge :chartID="'e3'" :chartBuild="{'colors':['#00aaee'],'ranges':[],'labels':['H2 RATIO',''],'labels':['N2 RATIO',''],'data':[panelData.n2Ratio.toFixed(5)*100,n2Remainder*100]}" :chartCenter="(panelData.n2Ratio*100).toFixed(3)+'%'"/>
            <div class='chart__title'>N<sub>2  (0-100%)</sub></div>
        </div>
        <div class='chart__container'>
             <chart-gauge :chartID="'e4'" :chartBuild="{'colors':['#00aaee'],'ranges':[],'labels':['H2 RATIO',''],'data':[panelData.h2Ratio.toFixed(5)*100,h2Remainder*100]}" :chartCenter="(panelData.h2Ratio*100).toFixed(3)+'%'"/>
            <div class='chart__title'>H<sub>2</sub> (0-1%)</div>
        </div>        
        <div class='chart__container'>
            <chart-gauge :chartID="'e5'" :chartBuild="{'colors':['#00aaee'],'ranges':[],'labels':['CH4 RATIO',''],'labels':['CH4 RATIO',''],'data':[panelData.ch4Ratio.toFixed(5)*100,ch4Remainder*100]}" :chartCenter="(panelData.ch4Ratio*100).toFixed(3)+'%'"/>
            <div class='chart__title'>CH<sub>4</sub> (0-1%)</div>
        </div>

        <div class='chart__container'>
            <chart-gauge :chartID="'e6'" :chartBuild="{'colors':['#00aaee'],'ranges':[],'labels':['H2 RATIO',''],'labels':['H2O RATIO',''],'data':[panelData.h2oRatio.toFixed(5),h2oRemainder]}" :chartCenter="(panelData.h2oRatio*100).toFixed(3)+'%'"/>
            <div class='chart__title'>H<sub>2</sub>O Vapor (0-100%)</div>
        </div>

    </div>    
</template>

<script>
import {mapState,mapMutations,mapGetters} from 'vuex'
import ChartGauge from '@/components/Chart-Gauge'
export default {

    data(){
        return{

        }
    },

    components:{
        'chart-gauge':ChartGauge,
    },

    computed:{
        ...mapGetters({
            panelData:'getEcosystem',
        }),

        co2Remainder: function(){            
            return Math.max(0,(.03 - this.panelData.co2Ratio).toFixed(5));
        },

        o2Remainder: function(){            
            return Math.max(0,(1.0 - this.panelData.o2Ratio).toFixed(5));
        },
        
        h2oRemainder: function(){
            return Math.max(0,(1.0 - this.panelData.h2oRatio).toFixed(5));
        },

        h2Remainder: function(){
            return Math.max(0,(.01 - this.panelData.h2Ratio).toFixed(5));
        },

        n2Remainder: function(){
            return Math.max(0,(1.0 - this.panelData.n2Ratio).toFixed(5));
        },

        ch4Remainder:function(){
            return Math.max(0,(.01 - this.panelData.ch4Ratio).toFixed(5));
        }

    }
}
</script>

<style lang="scss" scoped>
    .ecosystem-chart-content{
        color: white;
        display:grid;
        grid-template-rows: repeat(3,1fr);
        grid-template-columns: repeat(2,1fr);
        grid-row-gap: 8px;
        grid-column-gap:16px;
        height: 100%;
        width: 100%;
    }

    .chart__container{
        display:grid;
        height:100%;
        width:100%;
        grid-template-rows:2fr 1fr;
        grid-row-gap:8px;
    }

    .chart__title{
        text-align:center;
        font-family: 'roboto';
    }
</style>
