export default{
    state:{
        selectedWiki:'welcome',

        attributes:{
            'welcome':{
                'navItems':[{'link':'welcome','value':'welcome'},],
                'content':{'welcome':'Welcome to SIMOC<br><br>This is a private beta version.'},
            },
            'initial':{
                'navItems':[
                    {'link':'presets','value':'presets'},
                    {'link':'location','value':'location'},
                    {'link':'duration','value':'duration'}],
                'content':{'welcome':'Initial Welcome Message','presets':'','location':'','duration':''},
            },
            'inhabitants':{
                'navItems':[
                    {'link':'humans','value':'humans'},
                    {'link':'food','value':'food'},
                    {'link':'crew quarters','value':'crewquarters'},
                    {'link':'ECLSS','value':'eclss'}
                ],
                'content':{
                    'welcome':'Initial Inhabitants Welcome Message',
                    'humans':'<p>If you design a mission that includes humans in your off-world habitat, then several other components will be required: Food for the humans to consume as they travel from Earth to Mars and for their time on Mars until the greenhouse produces the first edible plants; an Environmental Control and Life Support System (ECLSS) to maintain breathable air and potable water, as well as manage the human waste products; and a place for the humans to work, recreate, and sleep.</p><br>Humans are not required for a mission configuration.',
                    'food':'Carefully consider the time required in transit from Earth to Mars, at roughly 7 months one-way. It is imperative that your humans have ample food supply for this journey, and enough food to sustain them on Mars as the plants are growing in the greenhouse, unable to produce edible fruit and vegetables until they mature.<br><br>While the total mass of the food required to bring the humans to Mars will be included in the payload calculations, the journey from Earth to Mars is not an integral part of this simulation. Therefore, at the launchof the simulation the food consumed during the transit will be immediately depleted from the total mass.<br><br>FOOD is not required if no humans are selected.',
                    'crewquarters':'In selecting your crew quarters, it is prudent to consider a balance between the minimum volume required to support the number of humans you have chosen for their physical comport, and the psychological benefits of a larger space. While SIMOC does not at this time model human social interactions, the further benefit of a larger crew quarters is that it holds more breathable air, and as such, acts as a storehouse for the key element for human survivalbreathing.<br><br>If you are able to bring a larger crew quarters on the first mission, this also allows you to add more humanswithout having to physically add structures. Future versions of SIMOC will enable the scaling of your habitat to accommodate a growing community.<br><br>CREW QUARTERS are not required if no humans are selected.',
                    'elcss':'An Environmental Control and Life Support System (ECLSS) is a set of machines whose function is to maintain clean air, water, and relative humidity while sustaining a comfortable temperature for human occupants of a closed environmental, such as a submarine, space craft, or off-world habitat.<br><br>ECLSS modules are composed of many interconnected components, each of which is modeled in SIMOC. But as they are also reach required, it is assumed they will be used together, as part of a total, working system. As such, you much choose how many ECLSS modules you will bring on your mission.<br><br>Each ECLSS module is designed to support three humans.<br><br>An ECLSS module is not required if no humans are selected.',
                }
            },
            'greenhouse':{
                'navItems':[
                    {'link':'greenhouse','value':'greenhouse'},
                    {'link':'plant species','value':'plantspecies'},                    
                ],
                'content':{
                    'welcome':'Initial Greenhouse Welcome Message',
                    'greenhouse':'As with the crew quarters, the size of the greenhouse is a choice. If you intent for your humans to consume only the food they have brought with them, and to rely fully on ECLSS modules, a greenhouse is not required. If you desire for your humans to consume some fresh fruits and vegetables, even if just one salad per few days, you might select a small greenhouse. The available plants will off-set some of the function of the ECLSS module(s). If you select a large greenhouse, you may have the option to eventually use the mechanical ECLSS modules as back-up only, and your humans might enjoy fresh food with every meal.<br><br>Keep in mind that if you select a large quantity of plants, you might require a larger greenhouse.<br><br>Each greenhouse comes includes an airlock in order that it can maintain an internal concentration of CO2 greater than that which is safe or comfortable for humans.<br><br>A GREENHOUSE is not required, even if humans are selected.',
                    'plantspecies':'If your elect to provide your human humans with something more interesting than freeze-dried food for their long stay on Mars, a greenhouse and plants is a welcomed respite from the monotony of meals in a squeeze bottles and aluminum foil.<br><br>Not only to fruits and vegetables provide nutrients and calories, they also help maintain a breathable atmosphere and clean water, off-setting, maybe even replacing the function of the ECLSS modules. When selecting your plants, from 1 to 6 species, you will also select the percentage of the total volume of the greenhouse allocated to each plant species.<br><br>Keep in mind that you will be starting your plants from seeds, not seedlings nor mature transplants. So make certain you have provided your humans with ample freeze-dried food for the journey to Mars, and during the time require before first harvest.<br><br>PLANTS are not required, even if humans are selected.',
                }
            },
            'power':{
                'navItems':[
                    {'link':'power generation','value':'powersource'},
                    {'link':'power storage','value':'powerstorage'},
                ],
                'content':{
                    'welcome':'Initial Power Welcome Message',
                    'powersource':'Select the number of solar photo-voltaic (PV) panels you believe will be required to sustain the electrical power needs of your full habitat. This includes electric heat, lighting for the human occupied space and for the plants in the greenhouse too, ECLSS operation, radio communication from the habitat to the orbiter, and personal computers.<br><br>On Mars, the total solar gain is roughly 1/3 that on Earth (at its maximum potential). Therefore, a 300 watt solar panel on Earth would generate only 100 watts on Mars. This means you will need 3x more panels to produce the same amount of electric power.<br><br>Keep in mind that not only do you need ample power for all electric devices by day, but also to charge the batteries to operate many of the same devices by night. This will result in a substantial quantity of solar PV panels in your total array.',
                    'powerstorage':'While solar PV panels generate power by day, it is the batteries that provide power at night. As such, the total battery capacity should be large enough to carry the habitat through an entire Martian night.',
                }
            },
        }

    },

    getters:{
        getWikiAttributes: state =>{
            console.log(state.attributes[state.selectedWiki]);
            return state.attributes[state.selectedWiki];
        }
    },

    mutations:{
        SETSELECTEDWIKI(state,value){
            state.selectedWiki = value;
        },
    }
}