<template>
    <div class='mission__stats'>
        <div>Location:</div>
        <div>Mars</div>
        <div>Inhabitants:</div>
        <div>{{humansRemaining}} Humans</div>
        <div>Duration Remaining:</div>
        <div>{{durationRemaining}}</div>
        <div>Sol Length:</div>
        <div>24.67 Hours</div>
        <div>Surface Temperature:</div>
        <div>210K | -63&#8451</div>
        <div>Solar Gain:</div>
        <div>500 W/m<sup>2</sup></div>
        <div>Atmospheric Pressure:</div>
        <div>0.636 kPa</div>
        <div>Gravity:</div>
        <div>7.11 m/s<sup>2</sup></div>
    </div>    
</template>

<script>
import {mapState,mapMutations,mapGetters} from 'vuex'
export default {
    components:{

    },

    computed:{
        ...mapGetters({
            stepData:'getMission',
            configData:'getMissionLength',
        }),

        durationRemaining:function(){
            let durationLength = this.configData;
            let currentStep = this.stepData.stepNumber;
            let currentSol = parseInt(this.stepData.stepNumber / 24.667);

            let conversion = {'hours':1,'sols':24.67,'years':8760}
            let string = "";
            let remaining = durationLength - currentSol;
            if(remaining <= 0){
                return "Mission Completed"
            } else{
                return remaining + " SOLS"
            }

            return remaining;            
        },

        humansRemaining:function(){
            let remaining = this.stepData.humans;

            if(remaining < 0 || remaining == null){
                return 0;
            } else{
                return remaining;
            }
        }
    }
}
</script>

<style lang="scss" scoped>
    .mission__stats{
        color: white;
        display:grid;
        height: 100%;
        width: 100%;
        grid-template-rows: repeat(auto-fit, 1.5rem);
        grid-template-columns: repeat(2, 1fr);
        grid-row-gap: 4px;

        *{
            color: white;
            font-size: 1rem;
            font-family: 'roboto'
        }
    }
</style>
