<template>
    <div class='dashboard'>
        <div class='dashboard__toolbar'>
            <div class='dashboard__toolbar__logo'>
                <img src='../assets/logo-horizontal.png'>
            </div>
            <keep-alive>
                <transition name='fade' mode='out-in'>            
                    <router-view name='toolbar' class='dashboard__toolbar--view' v-model='menuOpen'></router-view>
                </transition>
            </keep-alive>
        </div>
        <transition name='fade' mode='out-in'>
            <!--<keep-alive>-->
                <router-view name='content' class='dashboard__content dashboard__content--view'></router-view>
            <!--</keep-alive>-->
        </transition>
        <div class='menu__overlay' v-on:click='closeMenu()' v-if='menuOpen'/>
        <keep-alive>
            <router-view name='menu' v-model='menuOpen' v-if='menuOpen'></router-view>
        </keep-alive>
    </div>
</template>

<script>
import StepUpdateInterval from '@/javascript/stepUpdateInterval'
export default {
    data(){
        return{
            menuOpen:false,
        }
    },

    methods:{
        closeMenu(){
            StepUpdateInterval.PauseUpdateInterval();
            this.menuOpen = false;
        }
    }
}
</script>

<style lang="scss" scoped>
    @import '../scss/_simocTheme';
    .dashboard{
        width: 100%;
        height: 100%;
        display: grid;
        grid-template-columns: minmax(192px, 15vw) 1fr;
        grid-template-rows: 1fr;
        grid-column-gap: 32px;
        grid-template-amounts:'toolbar content';
        position: relative;
    }

    .dashboard__toolbar{        
        grid-amount:toolbar;
        width: 100%;
        height: auto;
        box-sizing: border-box;
        display: flex;
        flex-direction:column;
        justify-content:flex-start;
        align-items:center;

        .dashboard__toolbar--view{
            width: 100%;
        }
        .dashboard__toolbar__logo{
            @extend %display-panel;
            margin-bottom: 8px;
            box-sizing:border-box;
            height:auto;
            width: 100%;
        }

        img{
            width: 100%;
        }
    }

    .dashboard__content{
        grid-amount:content;
        width: 100%;
        height: 100%;
    }


    .menu__overlay{
        position: fixed;
        z-index: 1;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0,0,0,.5);
    }
</style>
