<template>
    <div class='content'>
        <div class='toolbar__clock'>
            <div class='clock__line__item'>
                <div>SOL:</div>
                <div>{{clockData.solNumber}}</div>
            </div>
            <div class='clock__line__item'>
                <div>STEP NUMBER:</div> 
                <div>{{clockData.stepNumber}}</div>
            </div>
        <!--</div>
        <div class='toolbar__speed'>-->
            <div class='toolbar__step__per clock__line__item'>
                <div>STEPS PER SECOND:</div>
                <div>{{stepRate}}</div>
            </div>
            <div class='toolbar__speed__control'>
                <fa-icon class='fa-icon' v-on:click='onDecreaseSpeed()' :icon="['fal','angle-double-left']"/>
                <fa-icon class='fa-icon play-icon' v-on:click='onPauseInterval()' :icon="['fal','play-circle']"/>
                <fa-icon class='fa-icon' v-on:click='onIncreaseSpeed()' :icon="['fal','angle-double-right']"/>
            </div>
        </div>
        <div class='toolbar'>
            <div class='toolbar__title'>
                SESSION DASHBOARD
            </div>
            <div class='toolbar__link' v-on:click='onDashboardMain()'>
                <fa-icon class='fa-icon' :icon="['fal','tachometer-alt']"/>
                <div class='link__item' >DASHBOARD MAIN</div> <!-- Fix -->
            </div>
            <div class='toolbar__link' v-on:click='onLiveAgentFeed()'>
                <fa-icon class='fa-icon' :icon="['fal','user-chart']"/>
                <div class='link__item' >LIVE AGENT FEED</div> <!-- Fix -->
            </div>
            <div class='toolbar__link disabled' disabled>
                <fa-icon class='fa-icon' :icon="['fal','rocket']"/>
                <div class='link__item'>PAYLOAD CREATOR</div>
            </div>
            <div class='toolbar__link disabled' disabled>
                <fa-icon class='fa-icon' :icon="['fal','book']"/>
                <div class='link__item'>SIMOC ENCYLOPEDIA</div>
            </div>
            <div class='toolbar__link disabled' disabled>
                <fa-icon class='fa-icon' :icon="['fal','leaf']"/>
                <div class='link__item'>GREENHOUSE</div>
            </div>
            <div class='toolbar__link disabled' disabled>
                <fa-icon class='fa-icon' :icon="['fal','cogs']"/>
                <div class='link__item'>SESSION CONFIGURATION</div>
            </div>
            <div class='toolbar__main' v-on:click='onOpenMenu()'>
                <fa-icon class='fa-icon' :icon="['fal','bars']"/>
                <div class='link__item'>MAIN MENU</div>
            </div>
        </div>
        
    </div>
</template>

<script>
import {mapState,mapMutations,mapGetters} from 'vuex'
import StepUpdateInterval from '@/javascript/stepUpdateInterval'
export default {
    data(){
        return{
            stepRate:1,
        }
    },

    computed:{
        ...mapGetters({
            clockData:'getClock'
        }),
    },



    methods:{
        onDashboardMain(){
            this.$router.push({ name:'session'})
        },

        onLiveAgentFeed(){
            this.$router.push({ name:'livefeed'})
        },

        onPauseInterval(){
            StepUpdateInterval.PauseUpdateInterval();
            this.stepRate = StepUpdateInterval.GetStepRate();
        },

        onDecreaseSpeed(){
            StepUpdateInterval.DecreaseUpdateSpeed();
            this.stepRate = StepUpdateInterval.GetStepRate();
        },

        onIncreaseSpeed(){
            StepUpdateInterval.IncreaseUpdateSpeed();
            this.stepRate = StepUpdateInterval.GetStepRate();
        },
        
        onOpenMenu(){
            StepUpdateInterval.PauseFromMenu();
            this.$emit('input',true);
        }
    }  
    
}
</script>

<style lang="scss" scoped>
    @import '../scss/_simocTheme';
    .content{
        height: 100%;
        display:flex;
        flex-direction:column;
        align-items: center;

        .toolbar__clock,.toolbar__speed{
            @extend %display-panel;         
            height: auto; 
            margin-bottom: 8px;
        }

        .toolbar__step__per,.toolbar__clock{
            color: white;
            font-family:'nasalization';
            font-size: .7rem;
            //text-align: center;
            width: 100%;
        }

        .toolbar__step__per{
            border-bottom:1px solid grey;
            padding-bottom: 8px;
        }

        .toolbar__clock{
            text-align:left;
            //text-align:center;
            margin-bottom: 8px;
            display:grid;
            grid-template-rows: repeat(2,1fr);
            grid-row-gap: 16px;
        }

        .toolbar__speed__control{
            display: flex;
            justify-content: space-around;
            align-items: center;
            color: white;
            font-size: 1.5rem;
        }
    }
    
    .clock__line__item{
        display: grid;
        grid-template-rows: repeat(auto-fit, auto);
        grid-template-columns: .7fr .3fr;
    }

    .toolbar{
        height: 100%;
        @extend %display-panel;
        display:flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;

        .toolbar__link{
            margin-bottom: 32px;
        }
    }

    .toolbar__title{
        font-size: 1.2rem;
        font-weight: 900;
        font-family:'nasalization';
        text-align: center;
        color: white;
        margin-bottom: 8px;
    }

    .toolbar__title::after{
        display:block;
        margin: 8px 0px;
        border-bottom: 1px solid grey;
        content:'';
    }

    .toolbar__link,.toolbar__main{
        width: 100%;
        display: grid;
        grid-template-columns: auto 1fr;
        grid-template-rows: repeat(auto-fit,auto);
        grid-column-gap: 16px;
        height: auto;
        font-size: .8rem;
        font-weight: 900;
        font-family:'nasalization';
        color: white;
        box-sizing: border-box;
        
        .link__item{
            margin: auto 0px;
            box-sizing:border-box;
        }

        .fa-icon{
            font-size: 1.5rem;
            margin: auto auto;
            box-sizing:border-box;
        }
    }

    .toolbar__link:hover:not([disabled]),.toolbar__main:hover:not([disabled]){
        color: #00aaee;
        cursor: pointer;
    }

    .play-icon{
        font-size: 2rem;
    }

    .toolbar__main{
        margin-top: auto;
        font-size: 1rem;
    }

    .toolbar__main::before{
        display:block;
        content:'';
        border-top: 1px solid grey;
        margin: 16px 0px;
        width: 100%;
        grid-column: span 2;
    }

    .disabled{
        color: grey;
    }

    .active{
        color: #53899e;
    }



</style>
