<template>
    <div class='helper__container'>
        <div class='title__bar'>GREENHOUSE ALLOCATION</div>
        <div class='helper__greenhouse'>                
            <chart-pie :chartCenter="greenhouseChartCenter" :chartID ="'1'" :chartColors="['#3c3c3c','#fdb53f','#3598dc','#1bbc9b','#fe7877','#f15928','#674ab4']" :chartBuild="greenhouseChartData"/>
        </div>   
    </div>
</template>

<script>
import {mapState,mapMutations,mapGetters} from 'vuex'
import ChartPie from '@/components/Chart-Greenhouse'
export default {

    components:{
        'chart-pie':ChartPie,
    },

    computed:{
        ...mapGetters({
            greenhouseRemainingSpace:'calcGreenhouseRemaining',
            greenhouseAvailableSpace:'greenhouseAvailable',
            plantSpecies:'getPlantSpecies',
        }),

        greenhouseChartCenter:function(){
            let str = '';
            str = this.greenhouseRemainingSpace + ' / ' + (this.greenhouseAvailableSpace);
            var text = str;
            return str;
        },

        greenhouseChartData:function(){
            let chartData = {
                'labels':[],
                'data':[]
            }

            chartData.labels.push('FREE SPACE');
            chartData.data.push(this.greenhouseRemainingSpace);

            let plants = this.plantSpecies;

            plants.forEach(element =>{
                if(element.species.length > 0 && element.species != 'none'){
                    chartData.labels.push(element.species.toUpperCase());
                    chartData.data.push(element.amount);
                    
                }
            })
            return chartData;
        },
        
        
        greenhouseUsedSpace: function(){
            let remainingSpace = this.greenhouseRemainingSpace;
            let availableSpace = this.greenhouseAvailableSpace;

            return availableSpace - remainingSpace;
        },




        greenhouseLabels: function(){
            let labels = ['Available Space'];
            
            this.plantSpecies.forEach(element =>{
                if(element.species != null && element.species != '' && element.species != 'none')
                    labels.push(element.species);
            })
            return labels;
        },

        greenhouseData:function(){
            let data = new Array();
            data.push(this.greenhouseRemainingSpace);

            this.plantSpecies.forEach(element =>{
                if(element.species != null && element.species != '' && element.species != 'none')
                    data.push(element.amount);
            })

            return data;
        }
    }
}
</script>

<style lang="scss" scoped>
    @import '../scss/_simocTheme';
    .helper__greenhouse{
        height: 100%;
    }

    .helper__container{
        @extend %display-panel;
        display:grid;
        grid-template-columns: 1fr;
        grid-template-rows: auto 1fr;
        grid-row-gap: 16px;
    }

    .title__bar{
        display: flex;
        justify-content: space-between;
        align-items:center;
        width: 100%;
        color: white;
        border-bottom: 1px solid grey;
        padding-bottom: 4px;
        font-size: .75rem;
        font-family:'nasalization';
        color: lightgreen;
    }
    
</style>

